.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.auto-n_seconds {
  position: fixed;
  color: aqua;
  background-color: blue;
  z-index: 10;
}
.auto-n_seconds:hover {
  cursor: pointer;
  color: white;
  background-color: darkblue;
}